* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

body {
  width: 100%;
  background: var(--bgColor);
  position: relative;
  color: var(--whiteColor);
}

.container {
  width: 100%;
  max-width: 1024px;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding: 0 15px;
  overflow: hidden;
}

a {
  text-decoration: none;
}

.btn-link {
  color: var(--whiteColor);
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 5px;
  font-size: 0.8rem;
  border-bottom: 1px solid #689af8;
  padding: 10px 10px 10px 0;
  transition: all .5s cubic-bezier(0.075, 0.82, 0.165, 1 );
  cursor: pointer ;
}

.btn-link:hover {
  padding: 10px;
  background: rgba(104, 154, 248, 0.102);
}
section {
  margin-top: 30px;
  margin-bottom: 100px;
}

ul {
  list-style: none;
}

h3 {
  font-size: 2rem;
  letter-spacing: 2px;
}